@import '../../../../styles/variables';

.sort {
  position: relative;
  cursor: pointer;
  margin-bottom: 15px;
  margin-right: 15px;
  &__label {
    display: flex;
    align-items: center;

    img {
      margin-right: 8px;
    }

    b,
    span {
      user-select: none;
    }

    b {
      margin-right: 8px;
      cursor: pointer;
    }

    span {
      color: $orange;
      border-bottom: 1px dashed $orange;
      cursor: pointer;
    }
  }
  &.active {
    .sort__popup {
      opacity: 1;
      transform: translateY(0);
    }
  }
  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 160px;
    opacity: 0;
    transition: all 0.3s ease;
    transform: translateY(10px);

    ul {
      overflow: hidden;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $orange;
        }
      }
    }
  }
}
